
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'


export default {
components: {
Group,
CheckIcon,
XHeader,
XInput,
XButton,
ViewBox,
Box,
Flexbox,
FlexboxItem
},
data () {
return {
showHeader: true,
isAccept: false,
telNum: '',
vcode: '',
reget: '',
password: '',
waiting: false,
counter: 59
}
},
methods: {
changeAccept () {
this.isAccept = !this.isAccept
},
doRegister () {
if (this.$refs.input1.valid && this.$refs.input2.valid && this.$refs.input3.valid) {
console.log('do register')
this.$api.userApi.registerUser(this.telNum.replace(/[ ]/g, ''), this.vcode, this.password).then(result => {
if (result.data.success) {
this.$vux.toast.show({
type: 'success',
text: ''
})
this.$router.replace({name: 'login'})
} else {
this.$vux.toast.show({
type: 'text',
text: result.data.message
})
}
})
} else {
this.$vux.toast.show({
type: 'text',
text: ''
})
}
},
login () {
this.$router.push({name: 'login'})
},
nextStep () {
this.$router.push({name: 'home'})
},
getSmsCode () {
if (this.$refs.input1.valid) {
console.log(this.telNum)
console.log(this.telNum.replace(/[ ]/g, ''))
this.$api.smsApi.getSmsCode(this.telNum.replace(/[ ]/g, ''), 'PHONELOGIN').then(result => {
if (result.data.success) {
this.waiting = true
this.timer = setInterval(this.changeBtnText, 1000)
this.$vux.toast.show({
type: 'success',
text: ''
})
} else {
this.$vux.toast.show({
type: 'warn',
text: result.data.message
})
}
})
} else {
this.$vux.toast.show({
type: 'text',
text: '',
width: '10em'
})
}
},
changeBtnText () {
this.reget = '(' + this.counter + ')'
this.counter--
if (this.counter < 0) {
this.waiting = false
this.reget = ''
window.clearInterval(this.timer)
this.counter = 59
}
}
},
computed: {
isInput () {
return this.vcode.length !== 4
}
}
}



